import React, {Component} from 'react'
// import * as BooksAPI from './BooksAPI'
import '../../App.css'

class ListBooklist extends Component {
  state = {
    /**
     * TODO: Instead of using this state variable to keep track of which page
     * we're on, use the URL in the browser's address bar. This will ensure that
     * users can use the browser's back and forward buttons to navigate between
     * pages, as well as provide a good URL they can bookmark and share.
     */
    showSearchPage: false
  }

 

  createTable = (books) => {
    let table = [];
    for (const book of books){
      const image = `url("${book.previewLink}")`;
      table.push(<li key={book.id}><div className="book">
      <div className="book-top">
      <div className="book-cover" style={{ width: 128, height: 188, backgroundImage: image }}></div>
      <div className="book-shelf-changer">
          <select>
          <option value="move" disabled>Move to...</option>
          <option value="currentlyReading">Currently Reading</option>
          <option value="wantToRead">Want to Read</option>
          <option value="read">Read</option>
          <option value="none">None</option>
          </select>
      </div>
      </div>
      <div className="book-title">{book.title}</div>
      <div className="book-authors">{book.authors}</div>
  </div></li>)
    }
    return table
  }

  render() {
    
    return (
        <div className="search-books-results">
            <ol className="books-grid">
                {this.createTable(this.props.books)}
            </ol>
        </div>
    )
  }
}
        
export default ListBooklist


